    z-index: -1;
    object-fit: cover;
    height: 100vh;
    
} */
.contentIntro{
    
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.hello{
    font-size: 1.75rem;
    font-weight: 200;
}

.introName{
    color: blue;
}

.introPara{
    font-size: medium;
    font-weight: 300;
    
}
.btn{
    background: white;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;

}
.btnImg{
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
}

@media screen and (max-width: 840px) {
    .contentIntro{
        font-size:10vw;
    }
    .hello{
        font-size: 4.5vw;
    }
}