.navbar{
    background: rgb(30, 30, 30);
    height:5rem ;
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content:space-between;
    position: sticky;
    top: 0;
    z-index: 3;
}

.logo{
    object-fit: cover;
    height: 3rem;
    width: 4rem;
   
}

.menuListItem{
    margin: 1rem;
    cursor: pointer;
}

.menuListItem:hover{
    color: blue;
    padding-bottom: .5rem;
    border-bottom: solid 1px blue;
}

.menuBtn{
    background: #fff;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem;
}
.menuImg{
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;
}

.active{
    color: blue;
    padding-bottom: 0.5rem;
    border-bottom: 3px solid blue;
    
}
.bmenu{
    display: none;
    object-fit: cover;
    height: 1.8rem;
}
.navMenu{
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background: rgb(40, 40, 40);
    border-radius: 1rem;

}

.listItem{
    padding: 0.5rem 3rem;
    margin: 0.25rem;
    background: rgb(30, 30, 30);
}

@media screen and (max-width: 720px) {

    .bmenu{
        display: flex;
    }

    .menuBtn{
        display: none;
    }
    .menuNav{
        display: none;
    }
    
}